:root {
  --font-family: Mulish;
  --font-title: Ubuntu;

  background: var(--color-bg-gradient);
  background-size: 1000% 1000%;
  animation: gradient 15s ease infinite;
  height: 100vh;

  --color-bg-gradient: linear-gradient(170deg, #6c42d5, #dfd9e4, #363ac8);
  --color-bg:#dfd9e4;
  --color-bg-extra-light:#f1efff;
  --color-bg-medium:#8676e4;
  --color-box-shadow-extra-light:#cec5f3f4;
  --color-box-shadow-light:#d7d2f668;
  --color-box-shadow-medium: #7a68dd7a;
  --color-box-extra-light: #e6e3f6f4;
  --color-bg-light:#cbc5f3;
  --color-underline:#a69bed;
  --color-bg-deep:#544889;
  --color-line-deep:#48315b;
  --color-footer : #031B34;
  --color-icon : #7f2fd4;
  --color-blog: #042c54;
  --color-text: #000000;
  --color-text-light: #fcfcfc;
  --color-text-title: #5b38c4;
  --color-link: #3f26a1;
  --color-subtext: #806492;
  --color-subtitle: #342c38;
  --color-link-transparent: rgba(88, 35, 145, 0.2);
  --color-box-shadow: rgba(139, 113, 151, 0.1);
  --color-button-light: #aaa0f1;
  --color-button-hover: #a9a0f139;


  --box-margin: 2rem 10rem 2rem;

}



@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

